<template>
  <div>
    <v-tooltip bottom>
      <template v-slot:activator="{ on: tooltip }">
        <v-btn
          :disabled="assignAdjDisabled"
          class="success mr-1"
          fab
          x-small
          v-on="{ ...tooltip }"
          @click="transferAdj()"
        >
          <v-icon>mdi-transit-transfer</v-icon>
        </v-btn>
      </template>
      <span>TRANSFER ADJUSTER</span>
    </v-tooltip>

    <!-- THE DIALOG BEGINS HERE -->

    <v-dialog v-model="dialogAdj" max-width="750px">
      <template v-if="editedCasefile">
        <v-card>
          <v-card-title class="justify-center text-h5 grey lighten-2">
            <span class="text-h5">TRANSFER ADJUSTER</span>
          </v-card-title>
          <v-card-text class="mt-3 pa-0">
            <v-container>
              <v-row>
                <v-col cols="12">
                  <v-card-title class="error">
                    BY TRANSFERING THE CASE TO ANOTHER ADJUSTER, PLEASE ACCEPT
                    THAT ALL PREVIOUS ADJUSTERS RELATED LOGS WILL BE DELETED!
                  </v-card-title>
                  <v-card-text class="mt-3 pa-0">
                    CHOOSE BRANCH FOLLOWED BY ADJUSTER
                  </v-card-text>
                </v-col>
                <v-col cols="12">
                  <v-select
                    v-model="branch.brCode"
                    :items="sortedBranches"
                    item-text="name"
                    item-value="brCode"
                    :menu-props="{ maxHeight: '400' }"
                    label="BRANCH"
                    hint="Select Branch"
                    persistent-hint
                    clearable
                  ></v-select>
                </v-col>
                <v-col cols="12">
                  <v-select
                    v-model="editedCasefile.adjuster"
                    :items="sortedAdjustersByBranch(branch.brCode)"
                    item-text="username"
                    item-value="id"
                    no-data-text="There is no Adjuster for the branch above"
                    :menu-props="{ maxHeight: '400' }"
                    label="ADJUSTERS"
                    hint="Assign Adjuster"
                    persistent-hint
                    clearable
                  ></v-select>
                </v-col>
                <v-col cols="12">
                  <v-spacer></v-spacer>
                  <v-btn
                    tile
                    small
                    dark
                    @click="closeAdj"
                    class="mt-3 mr-2 error"
                  >
                    Cancel
                  </v-btn>
                  <v-btn
                    tile
                    small
                    :disabled="assignAdjRule"
                    class="mt-3 success"
                    @click="saveAdjuster"
                    >Submit</v-btn
                  >
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-card>
      </template>
    </v-dialog>
  </div>
</template>

<script>
import CasefileDataService from "../../services/CasefileDataService";
import MailDataService from "../../services/MailDataService";
import moment from "moment";
import { mapState } from "vuex";

export default {
  name: "branchAccept",
  props: [
    "branches",
    // "branch",
    "management",
    "insurer",
    "refType",
    "subRefTypes",
    "members",
    "handlers",
    "states",
    "regions",
    "adjusters",
  ],
  components: {},

  data() {
    return {
      dialogAdj: false,
      menu: false,
      menu1: false,
      branch: {
        name: "",
        brCode: "",
      },
      editedCasefile: null, // Local copy for editing
    };
  },

  computed: {
    ...mapState(["caseState"]),

    isAddFileButtonDisabled() {
      return !(
        this.caseState.casefile.refType &&
        this.caseState.casefile.branch &&
        this.caseState.casefile.insurer &&
        this.caseState.casefile.claimNo &&
        this.caseState.casefile.vehicleNo &&
        this.caseState.casefile.dateOfLoss &&
        this.caseState.casefile.insComment
      );
    },

    assignAdjRule() {
      return !(
        this.branch.brCode &&
        this.editedCasefile &&
        this.editedCasefile.adjuster
      );
    },

    computedDateFormattedMomentjs() {
      return this.date ? moment(this.date).format("DD/MM/YY") : "";
    },

    assignAdjDisabled() {
      // return !this.management;
      return (
        this.caseState.casefile.fileStatus == `NEW` ||
        this.caseState.casefile.fileStatus == `PEND` ||
        this.caseState.casefile.fileStatus == `BRAC` ||
        this.caseState.casefile.fileStatus == `CANC` ||
        this.caseState.casefile.fileStatus == `CLO` ||
        !this.management
      );
    },
    // assignAdjDisabled() {
    //   return !(this.caseState.casefile.fileStatus == `BRAC` && this.management);
    // },
    sortedBranches() {
      // Sort branches by name and display in uppercase
      return [...this.branches]
        .map((b) => ({
          ...b,
          name: b.name ? b.name.trim().toUpperCase() : "",
        }))
        .sort((a, b) => a.name.localeCompare(b.name));
    },
    sortedAdjustersByBranch() {
      // Returns a function to filter and sort adjusters by branch code
      return (brCode) => {
        return this.adjusters
          .filter(
            (r) =>
              Array.isArray(r.branches) &&
              r.branches.some((i) => i.brCode === brCode)
          )
          .map((adj) => ({
            ...adj,
            username: adj.username ? adj.username.trim().toUpperCase() : "",
          }))
          .sort((a, b) => a.username.localeCompare(b.username));
      };
    },
  },
  watch: {
    transferAdj(val) {
      val || this.closeAdj();
    },
  },

  methods: {
    currentUserBranch() {
      if (
        this.$store.state.auth.user.roles.includes("MANAGER") ||
        this.$store.state.auth.user.roles.includes("ADMIN") ||
        this.$store.state.auth.user.roles.includes("CLERK")
      ) {
        return this.branches;
      } else
        return this.branches.filter(
          (x) => x.id === this.caseState.casefile.branch
        );
    },
    // moment: function (date) {
    //   return moment(date);
    // },
    filtersubRefType(item) {
      return this.subRefTypes.filter(
        (subRefType) => subRefType.deptId === item
      );
    },
    filterHandler(item) {
      return this.handlers.filter((handler) => handler.insId === item);
    },
    filterBranchName(item) {
      if (!item) return "TBA";
      const filteredbranch = this.branches.filter(
        (branch) => branch.id === item
      );
      return filteredbranch[0].name;
    },
    filterBranches(item) {
      return this.branches.filter((branch) => branch.id === item);
    },

    filterRegions(item) {
      return this.regions.filter((region) => region.branchId === item);
    },
    filterPicID(item) {
      if (!item) return "TBA";
      const filteredDept = this.refType.filter((dept) => dept.id === item);
      return filteredDept[0].picID;
    },

    filterDept(item) {
      return this.refType.filter((dept) => dept.id === item);
    },
    filterSubDept(item) {
      return this.subRefTypes.filter((subRef) => subRef.id === item);
    },
    filterIns(item) {
      return this.insurer.filter((insurer) => insurer.id === item);
    },
    findAdjEmail(item) {
      const adjuster = this.adjusters.filter(
        (adjuster) => adjuster.id === item
      );
      return (
        adjuster.map((b) => b.email) || "sys.admin@associatedadjusters.com"
      );
    },
    findUserEmail(item) {
      const user = this.members.filter((member) => member.id === item);
      return user[0] ? user[0].email : "sys.admin@associatedadjusters.com";
      // return user.map((b) => b.email);
    },

    filterAdjByBranch(item) {
      return this.adjusters.filter((r) =>
        r.branches.some((i) => i.brCode === item)
      );
    },

    transferAdj() {
      // Clone casefile for local editing
      this.editedCasefile = JSON.parse(JSON.stringify(this.caseState.casefile));
      // Reset branch selection to match casefile (if exists)
      if (this.caseState.casefile.branch) {
        const branchObj = this.branches.find(
          (b) => b.id === this.caseState.casefile.branch
        );
        this.branch = branchObj
          ? { name: branchObj.name, brCode: branchObj.brCode }
          : { name: "", brCode: "" };
      } else {
        this.branch = { name: "", brCode: "" };
      }
      this.dialogAdj = true;
    },

    saveAdjuster() {
      if (!this.editedCasefile) return;
      // Use local copy for submission
      const payload = {
        ...this.editedCasefile,
        dateOfAdj: moment().format(),
        assignAdjBy: this.$store.state.auth.user.id,
      };
      CasefileDataService.update(this.editedCasefile.id, payload)
        .then((response) => {
          this.closeAdj();
          this.$store.dispatch("setSnackbar", {
            showing: true,
            color: "warning",
            text:
              (response && response.data && response.data.message) ||
              toString(),
          });
          this.caseState.casefile = payload; // update global after success
        })
        .catch((e) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
        })
        .then(() => {
          const assignedAdjuster = payload.adjuster;
          var dataEmail = {
            id: payload.id,
            refType: this.filterDept(payload.refType)[0].description,
            refTypeCode: this.filterDept(payload.refType)[0].name,
            subRefType: this.filterSubDept(payload.subRefType)[0].subCodeDesc,
            subRefCode: this.filterSubDept(payload.subRefType)[0].subCode,
            branch: this.filterBranchName(payload.branch),
            branchCode: this.filterBranches(payload.branch)[0].brCode,
            insurer: this.filterIns(payload.insurer)[0].name,
            recipient: this.findAdjEmail(assignedAdjuster),
            createdBy: this.$store.state.auth.user.id,
          };
          MailDataService.assignAdj(dataEmail)
            .then((response) => {
              this.closeAdj();
              this.$store.dispatch("setSnackbar", {
                showing: true,
                color: "warning",
                text:
                  (response && response.data && response.data.message) ||
                  toString(),
              });
            })
            .catch((e) => {
              console.log(e);
            });
          this.dataEmail = "";
        })
        .catch((e) => {
          this.$store.dispatch("setSnackbar", {
            showing: true,
            text:
              (e.response && e.response.data && e.response.data.message) ||
              e.message ||
              toString(),
          });
        });
      this.closeAdj();
    },

    closeAdj() {
      this.dialogAdj = false;
      this.editedCasefile = null;
      this.branch = { name: "", brCode: "" };
      this.$nextTick(() => {
        // this.caseState.casefile.fileStatus = "NEW";
      });
    },
  },
};
</script>
